.container--note-page {
    height: 100%;
    padding: 10px;
    box-sizing: border-box
}

.container--header-toolbar {
    display: flex;   
}

.container--circle-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25px;
    width: 25px;
    margin-left: auto;
}

.container--note-body{
    height: calc(100% - 25px);
    overflow: hidden;
    padding: 5px;
}

blockquote {
    border-left: 2px solid #ddd;
    margin-left: 0;
    margin-right: 0;
    padding-left: 10px;
    font-style: italic;
}

.text-view-content--separator {
    font-size: 12px;
    line-height: 14px;
    vertical-align: middle;
    color: #5f6368;
}

.container--info {
    margin: 5px 0 0 0;
}